'use client';

import { motion } from 'framer-motion';
import { Sparkles, Bug, Plus, Shield, Zap, Users, Trophy, Star, Gamepad2, Wrench, ArrowUp } from 'lucide-react';

interface Patch {
  version: string;
  date: string;
  type: 'major' | 'minor' | 'hotfix';
  features: string[];
  improvements: string[];
  fixes: string[];
}

export default function CyberWorldPatchesPage() {
  const patches: Patch[] = [
    {
      version: '2.0.0',
      date: 'January 10, 2026',
      type: 'major',
      features: [
        'Complete visual overhaul with concept art matching graphics',
        '20+ detailed NPCs throughout the plaza with speech bubbles',
        'Massive detailed plaza with 15+ buildings, neon signs, and ambient effects',
        'Enhanced player avatars with cyber-arms, backpacks, visors, and armor',
        'Detailed market stalls with vendors and animated items',
        'Flying data drones with propellers and navigation lights',
        'Environmental signs and posters matching concept art',
        'Comprehensive dashboard with statistics and leaderboards',
        'Complete settings page with audio, graphics, controls, account, and privacy options',
        'Integrated directly into main website (no external links)'
      ],
      improvements: [
        'Improved NPC AI with patrolling and interaction systems',
        'Enhanced ambient effects with 120+ particles and multiple glow layers',
        'Better performance optimization for detailed graphics',
        'More responsive UI elements with animations',
        'Improved audio system with separate volume controls'
      ],
      fixes: [
        'Fixed syntax errors in ConceptPlaza implementation',
        'Resolved asset loading issues',
        'Fixed NPC speech bubble rendering',
        'Corrected building entrance scanner animations'
      ]
    },
    {
      version: '1.5.0',
      date: 'January 5, 2026',
      type: 'minor',
      features: [
        'Enhanced player avatars with detailed cyberpunk design',
        'Improved NPC interaction system with speech bubbles',
        'Added flying data drones to plaza environment',
        'Market stall vendors with animated items',
        'Environmental signs and posters throughout rooms',
        'Improved ambient particle effects (60+ particles)',
        'Enhanced building neon signs with animations',
        'Street details including bike racks and lights',
        'Updated quest system with 25+ quests',
        'Improved combat UI and feedback'
      ],
      improvements: [
        'Better performance optimization',
        'Improved network synchronization',
        'Enhanced visual feedback',
        'More responsive controls',
        'Better error handling'
      ],
      fixes: [
        'Fixed avatar rendering issues',
        'Resolved NPC positioning bugs',
        'Fixed market stall item display',
        'Corrected building entrance detection'
      ]
    },
    {
      version: '1.4.0',
      date: 'December 28, 2025',
      type: 'minor',
      features: [
        'Initial plaza implementation with basic buildings',
        'Player avatar system with customization',
        'Basic NPC system (10 NPCs)',
        'Turn-based combat system',
        'Quest system with 15+ quests',
        'Inventory and item management',
        'Chat system with emoji wheel',
        'Character creation and tutorial'
      ],
      improvements: [
        'Stable multiplayer connection',
        'Improved room loading',
        'Better error messages',
        'Optimized asset loading'
      ],
      fixes: [
        'Fixed connection issues',
        'Resolved room loading bugs',
        'Fixed avatar synchronization',
        'Corrected quest tracking'
      ]
    },
    {
      version: '1.0.0',
      date: 'December 20, 2025',
      type: 'major',
      features: [
        'Added Global Network Map with fast travel system',
        'Implemented Mission Briefing UI',
        'Added Extended Rooms (Orbital Casino, Cyber Jungle, Underwater Server, etc.)',
        'Enhanced speech bubbles with neon styling',
        'Added Achievement System',
        'Implemented Leaderboard System'
      ],
      improvements: [
        'Improved quest system with better tracking',
        'Enhanced combat system with more abilities',
        'Better character progression system'
      ],
      fixes: [
        'Fixed inventory display issues',
        'Resolved chat system bugs',
        'Fixed movement interpolation'
      ]
    },
    {
      version: '1.0.0',
      date: 'December 20, 2025',
      type: 'major',
      features: [
        'Initial release with Desktop OS interface',
        'CyberWorld launcher with stats display',
        'Isometric MMO scene with room system',
        'Real-time multiplayer with Socket.io',
        'Turn-based combat system',
        'Quest system with NPC interactions',
        'Inventory and item management',
        'Chat system with emoji wheel',
        'Character creation and tutorial'
      ],
      improvements: [],
      fixes: []
    }
  ];

  const getTypeIcon = (type: string) => {
    switch (type) {
      case 'major': return <Sparkles className="w-5 h-5" />;
      case 'minor': return <Plus className="w-5 h-5" />;
      case 'hotfix': return <Wrench className="w-5 h-5" />;
      default: return <Star className="w-5 h-5" />;
    }
  };

  const getTypeColor = (type: string) => {
    switch (type) {
      case 'major': return '#00f3ff';
      case 'minor': return '#00ff41';
      case 'hotfix': return '#ffaa00';
      default: return '#888';
    }
  };

  return (
    <div className="min-h-screen bg-[#0a0a0a] text-white font-mono p-8">
      <div className="max-w-6xl mx-auto">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          className="mb-8"
        >
          <h1 className="text-5xl text-[#00f3ff] mb-2 font-bold" style={{ textShadow: '0 0 30px rgba(0,243,255,0.8)' }}>
            üìù PATCH NOTES
          </h1>
          <p className="text-[#00ff41] text-lg">Complete changelog and updates</p>
        </motion.div>

        {/* Patches */}
        <div className="space-y-6">
          {patches.map((patch, index) => (
            <motion.div
              key={patch.version}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: index * 0.1 }}
              className="bg-[rgba(10,10,10,0.95)] border-2 rounded-lg p-6"
              style={{ borderColor: getTypeColor(patch.type) }}
            >
              {/* Patch Header */}
              <div className="flex items-center justify-between mb-6 pb-4 border-b" style={{ borderColor: `${getTypeColor(patch.type)}40` }}>
                <div className="flex items-center gap-4">
                  <div className="text-4xl" style={{ color: getTypeColor(patch.type) }}>
                    {getTypeIcon(patch.type)}
                  </div>
                  <div>
                    <h2 className="text-3xl font-bold" style={{ color: getTypeColor(patch.type) }}>
                      Version {patch.version}
                    </h2>
                    <p className="text-[#888] text-sm mt-1">{patch.date}</p>
                  </div>
                </div>
                <span className="px-4 py-2 rounded-lg font-bold uppercase text-sm" style={{ 
                  backgroundColor: `${getTypeColor(patch.type)}20`,
                  color: getTypeColor(patch.type),
                  border: `2px solid ${getTypeColor(patch.type)}`
                }}>
                  {patch.type}
                </span>
              </div>

              {/* Features */}
              {patch.features.length > 0 && (
                <div className="mb-6">
                  <h3 className="text-xl text-[#00ff41] mb-4 font-bold flex items-center gap-2">
                    <Zap className="w-5 h-5" />
                    New Features
                  </h3>
                  <ul className="space-y-2 ml-6">
                    {patch.features.map((feature, i) => (
                      <li key={i} className="text-[#aaa] flex items-start gap-2">
                        <span className="text-[#00ff41] mt-1">‚ñ∂</span>
                        <span>{feature}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              )}

              {/* Improvements */}
              {patch.improvements.length > 0 && (
                <div className="mb-6">
                  <h3 className="text-xl text-[#00d4ff] mb-4 font-bold flex items-center gap-2">
                    <ArrowUp className="w-5 h-5" />
                    Improvements
                  </h3>
                  <ul className="space-y-2 ml-6">
                    {patch.improvements.map((improvement, i) => (
                      <li key={i} className="text-[#aaa] flex items-start gap-2">
                        <span className="text-[#00d4ff] mt-1">‚ñ∂</span>
                        <span>{improvement}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              )}

              {/* Fixes */}
              {patch.fixes.length > 0 && (
                <div>
                  <h3 className="text-xl text-[#ffaa00] mb-4 font-bold flex items-center gap-2">
                    <Bug className="w-5 h-5" />
                    Bug Fixes
                  </h3>
                  <ul className="space-y-2 ml-6">
                    {patch.fixes.map((fix, i) => (
                      <li key={i} className="text-[#aaa] flex items-start gap-2">
                        <span className="text-[#ffaa00] mt-1">‚ñ∂</span>
                        <span>{fix}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </motion.div>
          ))}
        </div>

        {/* Coming Soon */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.5 }}
          className="mt-8 bg-[rgba(10,10,10,0.95)] border-2 border-[#ff00ff] rounded-lg p-6"
        >
          <h2 className="text-2xl text-[#ff00ff] mb-4 font-bold flex items-center gap-2">
            <Gamepad2 className="w-6 h-6" />
            Coming Soon
          </h2>
          <ul className="space-y-2 ml-6 text-[#aaa]">
            <li className="flex items-start gap-2">
              <span className="text-[#ff00ff] mt-1">‚ñ∂</span>
              <span>Pet system with cyber companions</span>
            </li>
            <li className="flex items-start gap-2">
              <span className="text-[#ff00ff] mt-1">‚ñ∂</span>
              <span>Housing system with customizable data havens</span>
            </li>
            <li className="flex items-start gap-2">
              <span className="text-[#ff00ff] mt-1">‚ñ∂</span>
              <span>Additional minigames (Code Breaker, Firewall Defender, etc.)</span>
            </li>
            <li className="flex items-start gap-2">
              <span className="text-[#ff00ff] mt-1">‚ñ∂</span>
              <span>Guild system with team quests</span>
            </li>
            <li className="flex items-start gap-2">
              <span className="text-[#ff00ff] mt-1">‚ñ∂</span>
              <span>Seasonal events and limited-time content</span>
            </li>
          </ul>
        </motion.div>
      </div>
    </div>
  );
}
